import React, { useState } from 'react';
import * as iscp from '@aptpod/iscp-ts'

const sleepMs = (ms: number) => new Promise<void>((resolve) => setTimeout(resolve, ms))

const App: React.FC = () => {
    const [urlString, setUrlString] = useState('https://intdash-cluster-dev.intdash.jp');
    const [token, setToken] = useState('JKQu4HiTcQmAvu279QXusMwwzejGp5nFpIMIhXhiq8Gh9sSA.4kXTu7b81f6HihHBMS1cShDLHy4fjvEm3vuDJ4IoAEU');
    const [nodeId, setNodeId] = useState('29866475-8a4d-45cb-9be7-5abac4382c6c');
    const [result, setResult] = useState('');
    const [isLoading, setIsLoading] = useState(false);

    const handleUrlStringChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setUrlString(event.target.value);
    };

    const handleTokenChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setToken(event.target.value);
    };

    const handleNodeIdChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setNodeId(event.target.value);
  };

    const fetchData = async () => {
        setIsLoading(true);
        try {
            const tokenSource = async () => {
              const response = await fetch(`${urlString}/api/iscp/tickets`, {
                  method: 'POST',
                  headers: {'X-Intdash-Token': token},
              });
              if (!response.ok) {
                  throw new Error('Network response was not ok');
              }
              const data = await response.json();
              return data["ticket"];
            }

            const url = new URL(urlString);
            console.log(url.protocol)
            const enableTLS = url.protocol=="https";
            const connector = new iscp.WebSocketConnector({enableTLS: enableTLS})

            const conn = await iscp.Conn.connect({
              address: url.hostname,
              connector,
              tokenSource,
              nodeId: nodeId,
            })

            const upstream = await conn.openUpstream({
              sessionId: 'sessionId',
            })

            await sleepMs(2000)


            setResult(JSON.stringify({"result": "done!"}));
        } catch (error) {
            console.log(error);
            setResult('通信に失敗しました。');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div>
            <input
                type="text"
                value={urlString}
                onChange={handleUrlStringChange}
                placeholder="URLを入力"
            />
            <input
                type="text"
                value={token}
                onChange={handleTokenChange}
                placeholder="APIトークンを入力"
            />
            <input
                type="text"
                value={nodeId}
                onChange={handleNodeIdChange}
                placeholder="ノードIDを入力"
            />
            <button onClick={fetchData} disabled={isLoading}>送信</button>
            {isLoading ? <div>ローディング中...</div> : <div><p>結果: {result}</p></div>}
        </div>
    );
};

export default App;
